  import Vue from 'vue'
  import Vuex from 'vuex'

  Vue.use(Vuex)

  export default new Vuex.Store({
    state: {
      isLogin: false,
      userInfo:'',
      fromPath:''
    }, mutations: {
      setLogin(state) {
          state.isLogin = true
         //如果登陆就可以获取用户信息
           state.userInfo=JSON.parse(window.sessionStorage.getItem("userInfo"))
      },setLogout(state){
        state.isLogin=false
        window.sessionStorage.removeItem("userInfo")
        state.userInfo=''
      },setTextNumSub(state){
        state.userInfo.userTextsnum--
      },setTextNumAdd(state){
        state.userInfo.userTextsnum++
      },setFocusNumSub(state){
        state.userInfo.userFocusnum--
      },setFocusNumAdd(state){
        state.userInfo.userFocusnum++
      },setFansNumSub(state){
        state.userInfo.userFansnum--
      },setFanusNumAdd(state){
        state.userInfo.userFansnum++
      },setCollectionsNumSub(state){
        state.userInfo.userCollectionsnum--
      },setCollectionsNumAdd(state){
        state.userInfo.userCollectionsnum++
      },setFromPath(state,c){
        state.fromPath=c
      }
    },
  })




