import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/index',
      component: resolve => require(['../components/common/Home.vue'], resolve),
      children: [
        {
          path: 'index',
          component: resolve => require(['../components/page/Index.vue'], resolve),
          meta: {
            title: "首页"
          }
        },
        {
          path: 'personHome/:userId',
          redirect: 'personHome/:userId/index',
          component: resolve => require(['../components/common/PersonHome.vue'], resolve),
          meta: {
            title: "个人中心"
          }, children: [
            {
              path: 'index',
              component: resolve => require(['../components/personal/Index.vue'], resolve),
              meta: {
                title: "个人空间"
              }
            },
            {
              path: 'collectionsList',
              component: resolve => require(['../components/personal/CollectionsList.vue'], resolve),
              meta: {
                title: "收藏列表"
              }
            }, {
              path: 'fansList',
              component: resolve => require(['../components/personal/FansList.vue'], resolve),
              meta: {
                title: "粉丝列表"
              }
            }, {
              path: 'focusList',
              component: resolve => require(['../components/personal/FocusList.vue'], resolve),
              meta: {
                title: "关注列表"
              }
            }
            , {
              path: 'textsList',
              component: resolve => require(['../components/personal/TextsList.vue'], resolve),
              meta: {
                title: "文章列表"
              }
            }, {
              path: 'editor',
              component: resolve => require(['../components/page/ReadText.vue'], resolve),
              meta: {
                title: "编辑文章"
              }
            }, {
              path: 'saveText',
              component: resolve => require(['../components/personal/SaveText.vue'], resolve),
              meta: {
                title: "发布文章"
              }
            }
          ]
        }, {
          path: 'text',
          component: resolve => require(['../components/page/ReadText.vue'], resolve),
          meta: {
            title: "查看文章"
          }
        }, {
          path: 'rank',
          component: resolve => require(['../components/page/Rank.vue'], resolve),
          meta: {
            title: "文章排行"
          }
        },{
          path:'search/:title',
          component:resolve=>require(['../components/page/Search.vue'],resolve),
          meta:{
            title:'查找文章'
          }
        }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: resolve => require(['../components/page/Login.vue'], resolve),
      meta: {
        title: "登陆"
      }
    }, {
      path: '/register',
      name: 'register',
      component: resolve => require(['../components/page/Register.vue'], resolve),
      meta: {
        title: "注册"
      }
    }
    ,
    {
      path: '*',
      name: '404',
      component: resolve => require(['../components/common/404.vue'], resolve),
      meta: {
        title: "页面不存在"
      }
    }
  ]
})
router.beforeEach((to, from, next) => {
  if (to.meta.title) {//如果设置标题，拦截后设置标题
    document.title = to.meta.title
  }
  const role = sessionStorage.getItem('userInfo');
   var accessPath=['/saveText']
   if(!role && to.path.indexOf(accessPath)!==-1&&to.path!=='/login'){
     next('/login');
   }
  if(to.path==='/login'){//记录是由哪个页面跳转到登陆页面的
    router.app.$options.store.commit('setFromPath',from.path)
  }
   next()
})
export default router




